import interfaces from '../../utils/interfaces.js';
var common = require('../../utils/common.js');
import app from "../../App.vue";
//时间走动
import jszd from "../../components/jszd.vue";
//弹窗
import pop from "@/components/ming-pop.vue";
export default {
	components:{
		jszd,
		pop
	},
	data() {
		return {
			tab:{
				name:["最新订单","待审核","已发布","已接单","待付款","待评价","已完成","已取消"],
				value:["new","0","1","2","3","4","5","9"]
			},
			tab_index:0,
			user_info: '',
			tip: '',
			sum: 0,
			list: [],
			page: 1,
			complete: true,
			//弹窗位置，默认上方
			direction: "up",
			//切换一口价或者报价下单的订单索引index
			price_index:0
		}
	},
	onShow() {
		this.user_info = app.globalData.user_info;
		if (this.user_info) {
			this.loadData(1);
			
		} else {
			wx.reLaunch({
				url: '/pages/auth/index',
			})
		}
	},
	/**
	 * 页面上拉触底事件的处理函数
	 */
	onReachBottom() {
	 	if (!this.complete) {
	 		this.page++;
	 		this.loadData(this.page);
		}
	},
	methods: {
		//拨打师傅电话
		callPhone(n) {
			wx.makePhoneCall({
				phoneNumber: n
			});
		},
		//获取数据
		loadData(page) {
			var status= this.tab.value[this.tab_index];
			this.request({
				url: interfaces.orderList,
				data: {
					page: page,
					status: status
				},
				header: {
					token: this.user_info.token
				},
				method: 'POST',
				success: res => {
					console.log(res);
					if (res.code == 200) {
						
						if (page == 1) {
							this.list = [];
						}

						this.sum=res.sum;

						if (!common.isBlank(res.data)) {
				
							this.list = this.list.concat(res.data);
							this.page = page;

							if (this.list.length >= res.sum) {
								this.complete = true;
								this.tip = '数据已全部加载，暂无其他数据';
							} else {
								this.complete = false;
								this.tip = '上拉加载更多';
							}
							
						} else {
							this.complete = true;
							this.tip = '数据已全部加载，暂无其他数据';
						}
						
					} else {
						app.isLogin(res.code, res.msg);
					}
				}
			})
		},
		tabChange(e) {
			this.tab_index=e.detail.value;
			this.loadData(1);
		},
		//普通客户取消订单
		cancel(id) {
			var _this = this;
			wx.showModal({
				title: '提示',
				content: '确认要取消订单吗？',
				success: function(res) {
					if (res.confirm) {
						_this.request({
							url: interfaces.orderCancel,
							header: {
								token: _this.user_info.token
							},
							data: {
								id: id
							},
							method: 'POST',
							success: res => {
								console.log(res);
								if (res.code == 200) {
									_this.tab_index = 7;
									_this.loadData(1);
								} else {
									app.isLogin(res.code, res.msg);
								}
							}
						})
					}
				}
			})
		},
		//取消的订单重新下单,订单到待审核状态
		recall(id){
			var _this = this;
			wx.showModal({
				title: '提示',
				content: '确认要重新下订单吗？',
				success: function(res) {
					if (res.confirm) {
						_this.request({
							url: interfaces.orderRecall,
							header: {
								token: _this.user_info.token
							},
							data: {
								id: id
							},
							method: 'POST',
							success: res => {
								console.log(res);
								if (res.code == 200) {
									_this.tab_index = 1;
									_this.loadData(1);
								} else {
									app.isLogin(res.code, res.msg);
								}
							}
						})
					}
				}
			})
		},
		//一口价改报价下单，报价下单改一口价订单
		change(id,type,index){
			var _this = this;
			if(type==1){
				//一口价下单改为报价下单
				wx.showModal({
					title: '提示',
					content: '确认要改为报价下单吗？',
					success: function(res) {
						if (res.confirm) {
							_this.request({
								url: interfaces.orderChange,
								header: {
									token: _this.user_info.token
								},
								data: {
									id: id,
									price:0
								},
								method: 'POST',
								success: res => {
									console.log(res);
									if (res.code == 200) {
										_this.list[index].type = 2;
									} else {
										app.isLogin(res.code, res.msg);
									}
								}
							})
						}
					}
				})
			}else{
				//报价下单改为一口价下单
				this.open('center');
				this.price_index=index;
			}
		},
		//报价下单修改价格
		editPrice(index){
			this.open('center');
			this.price_index=index;
		},
		//报价下单选择维修师傅
		select(order_id,baoming_id){
			var _this = this;
			//一口价下单改为报价下单
			wx.showModal({
				title: '提示',
				content: '确认要选择该师傅维修吗？',
				success: function(res) {
					if (res.confirm) {
						_this.request({
							url: interfaces.orderSelect,
							header: {
								token: _this.user_info.token
							},
							data: {
								order_id: order_id,
								baoming_id:baoming_id
							},
							method: 'POST',
							success: res => {
								console.log(res);
								if (res.code == 200) {
									_this.tab_index=3;
									_this.loadData(1);
								} else {
									app.isLogin(res.code, res.msg);
								}
							}
						})
					}
				}
			})
		},
		
		
		//表单提交事件，报价下单改为一口价下单，或者一口价下单修改价格
		formSubmit: function(e) {
			var _this = this;
			var data = e.detail.value;
			data.id=this.list[this.price_index].id;
			if (!data.price >0) {
				wx.showToast({
					title: '请输入正确的价格',
					icon: "none",
				});
				return;
			}
			this.request({
				url: interfaces.orderChange,
				data: data,
				header: {
					token: _this.user_info.token
				},
				method: 'POST',
				success: res => {
					console.log(res);
					if (res.code == 200) {
						wx.showToast({
							title: res.msg,
							icon: 'success'
						})
						this.list[this.price_index].total_price=data.price;
						this.list[this.price_index].type=1;
						this.$refs.pop.close();
					} else {
						app.isLogin(res.code, res.msg);
					}
				}
			});
		},
		
		//跳转订单详情页
		goDetail(id) {
			wx.navigateTo({
				url: 'detail?id=' + id,
			})
		},
		
		//跳转订单支付页
		goPay(id) {
			wx.navigateTo({
				url: 'pay?id=' + id,
			})
		},
		//弹窗组件
		open(direction) {
			this.direction = direction;
			//打开弹窗
			this.$refs.pop.show();
		}
	}
}
